(function (window, angular) {
    "use strict";

    var module = angular.module('menu', ['user', 'constants']);

    module.directive('cbMenu', [
        '$state', 'events', 'user', function ($state, events, user) {
          return {
            restrict: 'EA',
            replace: true,
            templateUrl: 'menu.html',
            link: function($scope) {
              $scope.menuExpanded = false;
              $scope.expandMenu = function() {
                $scope.menuExpanded = !$scope.menuExpanded;
              };

              $scope.user = null;
              $scope.$on(events.login, function (event, data) {
                $scope.user = data;
                $scope.$digest();
              });
              $scope.$on(events.logout, function () {
                $scope.user = null;
              });

              $scope.signoff = function () {
                user.destroy();
              };

              $scope.signup = function () {
                $state.go('signup');
              };

              $scope.signin = function () {
                $state.go('signin');
              };

              $scope.profile = function () {
                $state.go('profile');
              };
            }
          };
        }
    ])

})(window, window.angular);
(function (window, angular) {
    "use strict";

    var module = angular.module('profile', [
      'ui.router',
      'profiles-api',
      'user'
    ]);

    // Routes
    module.config([
      '$stateProvider', function ($stateProvider) {
        $stateProvider
          .state('profile', {
            url: '/profile',
            templateUrl: 'profile.html',
            controller: 'ProfileCtrl',
            data: {
              pageTitle: 'Your Profile on Clickberry'
            }
          });
        }
    ]);

    // Controllers
    module.controller('ProfileCtrl', [
      '$scope', '$state', 'profilesApi', 'user',
      function ($scope, $state, profilesApi, user) {

        $scope.profile = {};
        $scope.loading = false;

        $scope.submit = function (params) {
          $scope.loading = true;
          profilesApi.update(user.id, params.email, params.name, user.accessToken, function (err) {
            if (err) { throw err; }
            $scope.loading = false;
            $scope.$digest();
          });
        };

        (function load() {
          $scope.loading = true;
          profilesApi.get(user.id, user.accessToken, function (err, data) {
            if (err) { throw err; }
            $scope.profile = data;
            $scope.loading = false;
            $scope.$digest();
          });
        })();
      }
    ]);
})(window, window.angular);
